.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    flex-shrink: 0;
    background-color: black;
    padding: 50px;
    margin-top: 30px;

    &_agree {
        display: flex;
        text-align: center;
        color: white;
        font-size: 24px;
        font-weight: 500;
    }

    &_logo {
        width: 122px;
        margin-bottom: 50px;
    }
}

//!********* RESPONSIVE **********
@media screen and (max-width: 768px) {
    .footer {
        &_agree {
            font-size: 12px;
        }

        &_logo {
            margin-bottom: 30px;
        }
    }
}